import { z } from 'zod';
import { FiltersInterface, FiltersSchema } from './filters.interface';
import { FilterGroupTypesEnum } from '../type-enums';

export const FilterGroupSchema = <z.ZodType<FilterGroupInterface>>z.lazy(() =>
  z.object({
    type: z.nativeEnum(FilterGroupTypesEnum),
    filters: FiltersSchema,
  }),
);

export interface FilterGroupInterface {
  type: FilterGroupTypesEnum;
  filters: FiltersInterface;
}
